$lightgreen: #535938;
$darkgreen: #40473F;
$orange: #D9442A;

.home_bg{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background-image: linear-gradient($lightgreen, $darkgreen);
}
#next_btn_holder{
    width: 100%;
    display: flex;
    justify-content: center;
}


#landing_wrapping{
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    margin-top: 22%;
    #dob_capture{ 
        display: flex;
        flex-direction: column;
        align-items: center;
        position: absolute;
        left: 0;
        width: 100%;
        padding-top: 50px;
#next_btn_landing{
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: -140px;
    width: 100vw;
}
        .select_container{
            width: fit-content;
            height: fit-content;
            display: flex;
            justify-content: center;
            margin-top: 8vh;
            font-family: 'Fjalla One', sans-serif;
        .bb-container{ 
            width: 40vw;
            height: fit-content;
            display: flex;
            display: -webkit-flex;
            justify-content: flex-end;
            padding-right: 2px;
            .blackBox{ 
                margin-top: 1px;
                margin-right: 1px;
                // margin: 1px 1px 0 0;
                width: 13vw;
                height: 38px;
                position: absolute;
                background-color: black;
                z-index: 99;
                border-radius: 4px;
                display: flex;
                justify-content: center;
                align-items: center;
                .downarrow{
                    width: 60%;
                }
                .down{
                    position: absolute;
                }
                &:hover{ 
                    
                    cursor: pointer;
                }
            }
            
        }

        select{ 
            padding-left: 5px;
            position: absolute;
            margin-bottom: 20px;
            width: 40vw;
            height: 40px;
            font-size: 23px;
            background-color: #D9442A;
            color: white;
            border: none;
            font-family: 'Fjalla One', sans-serif;
        }
    }
    }
}


.bloodOrangeBg{
    background: url('./../Assets/new-bg.jpg') no-repeat center center !important;
    background-size: cover !important;
}

.Prizing{
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    background: url('./../Assets/data-capture-bg.png') no-repeat center center;
    background-size: cover;
    
    .titleContainer{
        width: auto;
        position: absolute;
        left: 0;
        top: 0;
        margin: 23vh 0px 0px 6vw;
    }
.blood_orange_h1{
    color: white;
    margin-bottom: 5px;
    font-weight: 500;
    
}
.Title2{
    margin-top: -2vh;
}
.ROR{
    margin-top: -1vh;
}
#next_btn_green{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding-top: 12%;
}
.columns_holder{ 
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
}
.shared_width{
    color: white;
    width: calc(100% / 2);
    display: flex;
    flex-direction: column;
   align-items: flex-end;
   padding-left: 6%;
   z-index: 99;

    .thin{
        font-weight: 200;
        font-family: 'Oswald', sans-serif;
    }
    .info_container{
        width: 150px;
        margin-top: 20px;
    }
}
.left-content{
    padding-left: 6vw;
    padding-top: 10vh;
    margin-top: 4vh;
    width: 41vw;
}
.margin_zero{
    margin: 0px;
   
}
}

#answer_list{
display: flex;
flex-direction: column;
align-items: center;
width: 100%;

.green_bg{
    width: 100%;
    height: fit-content;
    background-color: #5E643E;
    margin-top: 4%;
    text-align: center;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 5px 6px rgba(61, 61, 61, 0.664);
    .ans{
        font-size: 1em; 
        width: 90%;

        // padding: 0% 0px 0% 0px;
        height: fit-content;
    }
}
}
#title_holder{
    width: 80%;
    display: flex;
    justify-content: center;
    text-align: center;
    color: white;
    background-color: $lightgreen;
    padding: 0 16px;
}
#quiz_holder{
    margin-top: 18vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#quiz_holder_{
    margin-top: 25vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#title_holder_contact{
    width: 80%;
    display: flex;
    flex-direction: column;
    text-align: center;
    color: white;
    background-color: $lightgreen;
    h2{
        margin-bottom: 3px;
    }
    h4{
        margin: 0px 0px 5% 0px;
    }
}
.form_collection{ 
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    // margin-top: 20vh;
input{ 
    width: 75%;
    height: 20px;
//    border-radius: 0.6em;
padding: 10px;
   border: none;
   margin-top: 5px;
}
label{
    color: white;
    font-weight: bold;
}
.labels{
    width: 80%;
    margin-top: 5%;
}
#submit_btn{
    background-color:$lightgreen;
    border: none;
    font-weight: bold;
    font-family: 'Fjalla One', sans-serif;
    letter-spacing: 1px;
    color: white;
    width: 70%;
    height: 40px;
    margin-top: 7%;
    box-shadow: 0px 9px 12px rgba(0, 0, 0, 0.452);
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}
}

#vapePen{
    width: 39%;
    position: absolute;
    z-index: 0;
    right: 20px;
    top: 100px;

}
#sourBag{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
}
#ball_layer, #guitar_layer{
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background: url('./../Assets/ball.svg') no-repeat center center;
    background-size: contain;
#guitar{
    width: 90%;
    position: absolute;
    bottom: 0;
    margin-left: -20%;
}
}

.noOverflow{ 
    overflow-y: hidden;
}
#instruction_context{
    color: white;
    width: 40%;
    font-size: 1.2rem;
    position: absolute;
    top: 0;
    right: 0;
    margin: 30vh 5vw 0 0;

}
#contact_header_comment_holder{ 
    color: white;
    text-align: center;
    width: 70vw;
    margin-top:-10vh;
    h2{
        font-weight: 100;
        font-size: 25px;
    }

    h4{ 
        font-weight: 100;
    }

}
.input_container{
    width: 100vw;
    height: auto;
    display: flex;
    justify-content: center;
    margin-top: 4vh;
    .input_wrapper{
        display: flex;
        align-items: center;
        .label_bg{
            font-family: 'Fjalla One', sans-serif;
            letter-spacing: 1px;
            height: 30px;
            width: 30vw;
            color: white;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 2px solid $lightgreen;
            background-color: $lightgreen;
            .labels_{
                font-size: 13px;
            }
        }
        .formInput{ 
            margin-top: 0;
            border: 2px solid $lightgreen;
            height: 10px;
            color: $orange;
            font-size: bold;
            border-radius: 0px;
            appearance: none;
            -webkit-appearance: none;
            -moz-appearance: none;
        }
        width:70%;
    }
}











